.sign__button {
    background-color: #ffffff;
    color: black;
    min-height: 50px;
    border-radius: 2px;
    cursor: pointer;
    border: none;
    font-size: 18px;
    font-weight: 400;
    line-height: 1.2;
    padding: 0;
    margin: 170px 0 15px;
  }
  
  .sign__button:hover {
    opacity: 0.85;
  }
  @media screen and (max-width: 670px) {
    .sign__button {
      font-size: 16px;
      margin: 143px 0 20px;
      min-height: 46px;
    }
  }